<template>
  <div id="submit" v-if="Flag">
    <div class="submit_nav">
      <div class="submit_nav_s">
        <div class="submit_nav_l"><div class="submit_nav_text"></div></div>
        <div class="submit_nav_r" @click="shop">
          <div class="submit_nav_text"></div>
        </div>
      </div>
      <!-- 地址选择 -->
      <div
        class="submit_address"
        v-if="addressList.length"
        @click="show1 = true"
      >
        <div class="submit_address_l">
          <div>
            <span>{{ addressList[address_radio].real_name }}</span
            ><span>{{ addressList[address_radio].phone }}</span>
          </div>
          <div>
            <span v-if="addressList[address_radio].is_default">[默认]</span
            ><span class="black"
              >{{ addressList[address_radio].province
              }}{{ addressList[address_radio].city
              }}{{ addressList[address_radio].district
              }}{{ addressList[address_radio].detail }}</span
            >
          </div>
        </div>
        <div class="submit_address_r"><van-icon name="arrow" /></div>
      </div>
      <!-- 地址选择 -->
      <van-action-sheet v-model="show1" title="选择地址">
        <div class="content">
          <van-radio-group v-model="address_radio">
            <van-radio
              v-for="(i, index) in addressList"
              :key="i.id"
              label-position="left"
              :name="index"
              :class="index == address_radio ? 'active' : ''"
            >
              <template>
                <AddressPsd :item="i"></AddressPsd>
              </template>
              <!-- <template #icon="props">
                <van-icon class="img-icon" name="success" :color="props.checked?'#fc4141':'transparent'" />
              </template> -->
            </van-radio>
            <div class="add_btn">新加地址</div>
          </van-radio-group>
        </div>
      </van-action-sheet>
      <!-- 下面图片 -->
      <div class="submit_img">
        <img src="http://47.115.51.185/h5/img/line.05bf1c84.jpg" alt="" />
      </div>
    </div>
    <!-- 商品 -->
    <div class="submit_goods">
      <div class="num">共件商品</div>
      <ul class="godds_list">
        <li v-for="i in cartInfo" :key="i.id">
          <Psd :i="i" red="r"></Psd>
        </li>
      </ul>
    </div>
    <!-- 优惠券等选择 -->
    <div class="submit_choice">
      <van-cell
        is-link
        value="优惠券"
        @click="show = true"
        class="submit_choice_item"
      >
        <template #right-icon>
          <span>请选择</span>
          <van-icon name="arrow" class="arrow-icon" />
        </template>
      </van-cell>
      <van-action-sheet v-model="show" title="优惠券">
        <div class="content">
          <van-image
            fit="cover"
            src="http://47.115.51.185/h5/img/noCoupon.e524084b.png"
          />
        </div>
      </van-action-sheet>
      <!-- 积分抵扣 -->
      <div class="submit_choice_item submit_choice_item_1">
        <div class="submit_choice_item_l">积分抵扣</div>
        <div class="submit_choice_item_r" v-if="userInfo">
          <span>当前积分</span><span>{{ userInfo.integral }}</span
          ><van-checkbox v-model="checked" checked-color="#fc4141" />
        </div>
      </div>
      <div class="submit_choice_item submit_choice_item_1">
        <div class="submit_choice_item_l">快递费用</div>
        <div class="submit_choice_item_r" v-if="priceGroup">
          <span>{{
            priceGroup.storePostage == 0
              ? "免运费"
              : "￥" + priceGroup.storePostage
          }}</span>
        </div>
      </div>
      <div class="submit_longText">
        <p>备注信息</p>
        <textarea placeholder="请添加备注（150字以内）"></textarea>
      </div>
    </div>
    <!-- 支付方式 -->
    <div class="payment">
      <p>支付方式</p>
      <div class="payment_ipt">
        <van-radio-group v-model="radio">
          <van-radio name="1" :class="radio == 1 ? 'active' : ''"
            ><Pay
              cls="icon-weixinzhifu"
              color="#41b035"
              text="微信支付"
              text1="微信快捷支付"
            ></Pay
          ></van-radio>
          <van-radio name="2" :class="radio == 2 ? 'active' : ''"
            ><Pay
              cls="icon-balance"
              color="#fe960f"
              text="余额支付"
              text1="可用余额：0.00"
            ></Pay
          ></van-radio>
        </van-radio-group>
      </div>
    </div>
    <!-- 总价 -->
    <div class="sum_price">
      <!-- 商品总价 -->
      <p v-if="cartInfo">
        <span>商品总价：</span> <span>￥{{ total }}</span>
      </p>
      <!-- 运费 -->
      <!-- v-if="priceGroup.storePostage" -->
      <p v-if="priceGroup.storePostage">
        <span>运费</span><span>+￥{{ priceGroup.storePostage }}</span>
      </p>
      <!-- 积分抵扣 -->
      <p v-if="checked">
        <span>积分抵扣</span><span>-￥{{ userInfo.integral }}</span>
      </p>
    </div>
    <!-- 合计 -->
    <div class="total">
      <p>
        合计：<span>￥{{ payPrice }}</span>
      </p>
      <div><button @click="clos">立即结算</button></div>
    </div>
  </div>
</template>
<script>
// 订单商品
import Psd from "../components/OrderRefund/OrderGoodsPsd";
// 支付方式
import Pay from "../components/submit/Pay";
//地址选择模板
import AddressPsd from "../components/address/add/AddPsd";
export default {
  components: {
    Psd,
    Pay,
    AddressPsd,
  },
  data() {
    return {
      // 订单确认列表
      confirmList: null,
      // 订单标识符
      orderKey: null,
      // 订单商品信息
      cartInfo: null,
      // 订单价格信息
      priceGroup: null,
      //   默认地址
      address: null,
      //   选择要优惠券的显示与隐藏
      show: false,
      //   支付方式选择
      radio: "1",
      //   优惠券，邮费，减免，总价等信息
      priceList: null,
      //用户信息
      userInfo: null,
      // 积分抵扣按钮
      checked: true,
      // 最终支付价格
      payPrice: 0,
      // 地址选择
      show1: false,
      // 地址列表
      addressList: [],
      // 地址选择
      address_radio: 0,
      // 是否加载完毕
      Flag: false,
    };
  },
  created() {
    // 用户信息获取
    // this.axios.get("/api/user").then(res=>{
    //     console.log(res);
    // })
    // 订单确认
    let cartId = this.$route.params.id;
    this.axios.post("/api/order/confirm", { cartId: cartId }).then((res) => {
      this.Flag = true;
      // console.log(res);
      let data = res.data.data;
      // console.log("aaaa",data);
      // 订单列表
      this.confirmList = data;
      this.orderKey = data.orderKey;
      // 订单商品信息
      this.cartInfo = data.cartInfo;
      // console.log("aa", this.cartInfo);
      this.priceGroup = data.priceGroup;
      // console.log(this.priceGroup);
      //默认地址
      this.address = data.addressInfo;
      // console.log(this.address);
      // 用户信息
      this.userInfo = data.userInfo;
      this.end_pay(this.orderKey);
      // 地址列表
      // this.axios.get("/api/address/list").then((res) => {
      //   this.addressList = res.data.data;
      //   this.addressList.unshift(this.address);
      // });
    });
    // 优惠券，邮费，减免，总价等信息
    // this.axios.get("/api/order/computed/")
    // 地址列表
    this.axios.get("/api/address/list").then((res) => {
      this.addressList = res.data.data;
      this.addressList.sort((a, b) => {
        a = a.is_default;
        b = b.is_default;
        return b - a;
      });
      // console.log(this.addressList);
    });
  },
  methods: {
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      console.log(item);
      //   this.toast(item.name);
    },
    // 最终支付价格
    end_pay(key) {
      let useIntegral = this.checked ? 1 : 0;
      // 支付方式
      let type = this.radio == 1 ? "weixin" : "yue";
      this.axios
        .post("/api/order/computed/" + key, {
          addressId: this.addressList[this.address_radio].id,
          couponId: 0,
          payType: type,
          shipping_type: 1,
          useIntegral: useIntegral,
        })
        .then((res) => {
          // console.log(res);
          this.payPrice = res.data.data.result.pay_price;
        });
    },
    // 到店
    shop() {
      this.$toast({
        message: "暂无门店信息，您无法选择到店自提",
        icon: "close",
        duration: "350",
      });
    },
    // 结算
    clos() {
      let addressId = this.addressList[this.address_radio].id;
      // 是否使用了积分抵扣
      let useIntegral = this.checked ? 1 : 0;
      // 支付方式选择
      let type = this.radio == 1 ? "weixin" : "yue";
      this.axios
        .post("/api/order/create/" + this.orderKey, {
          addressId: addressId,
          useIntegral: useIntegral,
          payType: type,
        })
        .then((res) => {
          // console.log(res.data.data.result);
          // 订单id
          let orderId = res.data.data.result.orderId;
          this.$router.push({
            path: `/order/status/${orderId}`,
            query: {
              msg: res.msg,
            },
          });
        });
    },
  },
  watch: {
    // 是否选择了积分抵扣
    checked() {
      this.end_pay(this.orderKey);
    },
    // 地址选择
    address_radio() {
      // console.log(news);
      this.show1 = false;
    },
    // 支付方式选择
    radio() {
      this.end_pay(this.orderKey);
    },
  },
  computed: {
    total() {
      return this.cartInfo[0].truePrice * this.cartInfo[0].cart_num;
    },
  },
};
</script>
